import React, { useMemo, useEffect, useState, useRef } from 'react';
import Head from 'next/head';
import Router from 'next/router';
import { Twitter, Facebook } from 'react-social-sharing';

import Gist from 'react-gist';
// import AdSense from 'react-adsense';

import {
  // adClientId,
  domain,
} from '../../constants/constants';
import NavBar from '../../components/NavBar';
import { printDate } from '../../services/utils';
import RecentArticles from '../../components/RecentArticles';
import Footer from '../../components/Footer';
import VerticalAd from '../../components/VerticalAd';
import useBlogLoader from '../../services/useBlogLoader';

/**
 * This file is used only in development. Any change in this must be manually
 * copied into /pages/blog.js file to apply that in production
 */
function Blogs() {
  const id = 'mercury_the_innermost_planet';
  const blogTitle = 'Mercury the innermost planet';
  const blogDescription = 'Mercury is the closest planet to the sun. It is also the smallest planet in our solar system';

  const {
    blog,
    recent,
  } = useBlogLoader({
    id,
  });
  const shareLinks = useRef(null);
  const [socialLinks, setSocialLinks] = useState([{
    provider: 'linkedin',
    url: `https://www.linkedin.com/sharing/share-offsite/?url=http%3A%2F%2F${domain}`,
  }]);

  useEffect(() => {
    /**
     * Fetching the links to create own social share links
     */
    if (shareLinks.current && id && domain) {
      const blocks = shareLinks.current.querySelectorAll('a');
      const links = JSON.parse(JSON.stringify(socialLinks));
      for (let i = 0; i < blocks.length; i += 1) {
        if (blocks[i].href) {
          if (blocks[i].href.includes('twitter')) {
            links.push({
              provider: 'twitter',
              url: blocks[i].href,
            });
          } else if (blocks[i].href.includes('facebook')) {
            links.push({
              provider: 'facebook',
              url: blocks[i].href,
            });
          }
        }
      }
      setSocialLinks(links);
    }
  }, [shareLinks, id, domain]); // eslint-disable-line

  const renderBlog = (content) => {
    if (!content || content.length <= 0) return null;

    const bloger = content.map((line, index) => {
      if (line.isImage && line.imageUrl) {
        return (
          <div key={index} className="d-flex justify-content-center image">
            <img src={line.imageUrl} alt="" />
          </div>
        );
      }

      if (line.isGist && line.gist) return <Gist key={index} className="mt-3 mb-3" id={line.gist} />;

      if (line.isMainHeading) return <div className="main-heading" key={index} dangerouslySetInnerHTML={{ __html: line.html }} />;

      if (line.isSubHeading) return <div className="sub-heading" key={index} dangerouslySetInnerHTML={{ __html: line.html }} />;

      if (line.isCodeSection) return <div className="code-section" key={index} dangerouslySetInnerHTML={{ __html: line.html }} />;

      if (line.isQuoted) return <div className="quote" key={index} dangerouslySetInnerHTML={{ __html: line.html }} />;

      return <div key={index} dangerouslySetInnerHTML={{ __html: line.html }} />;
    });
    return bloger;
  };

  const onSocialShare = (url) => {
    if (url) window.open(url, '', 'width=500,height=500');
  };

  const printBlog = useMemo(() => {
    return renderBlog(blog.content);
  }, [blog.content])

  return (
    <>
      <NavBar />
      <Head>
        <title>{blogTitle}</title>
        <meta name="description" content={blogDescription} />

        <meta itemProp="name" content={blogTitle} />
        <meta itemProp="description" content={blogDescription} />
      </Head>
      <span className="blog-page">
        <div className="blog-page__container">
          {/* <Share urls={shareUrls} /> */}
          <VerticalAd />
          <div className="container">
            <div className="row">
              <div className="col-12 header-ad">
                {/* <AdSense.Google
                  client={adClientId}
                  slot="3072497734"
                  format="auto"
                  responsive="true"
                /> */}
              </div>
              <div className="col-12">
                <h1 className="heading-text">
                  {blogTitle}
                </h1>
                <p className="lead">
                  {printDate(blog.date)}
                                &nbsp;-&nbsp;
                  {blog.readTimeMin}
                  {' '}
                  mins read
                </p>
                <ul className="share">
                  <li ref={shareLinks} className="hide-links">
                    <Twitter link={`https://www.${domain}/${id}`} />
                    <Facebook link={`https://www.${domain}/${id}`} />
                  </li>
                  {socialLinks.map((links, index) => (
                    <li key={index}>
                      <a
                        aria-label={links.provider}
                        style={{ cursor: 'pointer' }}
                        className="mr-5"
                        rel="noopener noreferrer"
                        onClick={() => onSocialShare(links.url)}
                      >
                        <i className={`fab fa-${links.provider}`} />
                      </a>
                    </li>
                  ))}
                </ul>
              </div>
              <div className="contents col-12 col-sm-12 col-md-8 col-xl-8 col-lg-8">
                {printBlog ? printBlog : blogDescription}
              </div>
              <div className="d-none d-sm-none d-md-block d-xl-block d-lg-block" />
              <div className="col-12">
                <hr />
              </div>
              <div className="col-12">
                {/* <AdSense.Google
                  client={adClientId}
                  slot="9591641415"
                  format="auto"
                  responsive="true"
                /> */}
              </div>
            </div>
            <div className="recent">
              You might be interested in:
            </div>
            <RecentArticles articles={recent} />
          </div>
        </div>
      </span>
      <Footer />
    </>
  );
}

Blogs.getInitialProps = async function ({ Component, pageProps }) {
  return <Component {...pageProps} />;
};

export default Blogs;
